.tech {
    width: 50%;
}

.developer {
    text-transform: capitalize;
}

.techHeader {
    display: flex;
    width: 50%;
}
.techHeader span{
    width: 10px;
    margin-left: 5px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.developerHeader {
    display: flex;
    width: 50%;
}
.developerHeader span{
    width: 10px;
    margin-left: 5px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.row {
    width: 100%;
    max-width: 606px;
    min-height: 47px;
    display: flex;
    padding: 10px 17px;
    justify-content: flex-start;
    border-bottom: 1px solid #E5E5E5;
}

.rowHeader {
    margin-top: 36px;
    padding: 10px 17px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    max-width: 606px;
    height: 36px;
    background-color: #E5E5E5;
    text-transform: capitalize;
}

.loading {
    width: 100%;
    max-width: 606px;
    height: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255,255,255, 0.9);
}