[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    width: 100%;
    max-width: 131px;
    height: 36px;
    padding: 10px 8px;
}

.selectContainer{
    display: flex;
    flex-direction: column;
    gap: 6px;
}
